.App {
  text-align: center;
  max-width: 100vw;
  max-height: 100vh;
 }
  
 .container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: #282c34;
  color: white;
  font-family: sans-serif;
 }
  
 .left {
  display: flex;
  flex-direction: column;
  width: 70%;
  height: 100vh;
  background-color: #282c34;
  color: white;
  font-family: sans-serif;
  justify-content: flex-start;
  padding: 10px;
  align-items: center;
 }
  
 .right {
  display: flex;
  flex-direction: column;
  width: 30%;
  height: 100vh;
  background-color: #282c34;
  color: white;
  font-family: sans-serif;
  border-left: 2px solid #777;
  padding: 10px;
  overflow-y: scroll;
 }